body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.listStyle{
    width: calc(33.33% - 50px) ;
    list-style: none;
    display: flex;
    align-items: center;
    flex-direction: column;
    position: relative;
    margin: 25px;
}
.shippingText{
    position: absolute;
    right: 0;
    font-size: 10px;
    background: #000;
    color: #fff;
    padding: 5px;
}
.footerStyle {
    background: #000;
    color: #fff;
    padding: 10px;
    text-align: center;
    width: 100%;
    position: fixed;
    bottom: 0;
}
.headerStyle {
    background: #000;
    color: #fff;
    padding: 10px;
    text-align: center;
}
.imageStyle {
    height: 200px
}
.buttonStyle {
    background: #000;
    color: #fff;
    width: 80%;
    padding: 10px;
    font-size: 15px;
    cursor: pointer;
    outline: none;
    text-decoration: none;
    text-align: center;
}
.sizeSelection {
    background: #000;
    border-radius: 50%;
    padding: 10px;
    color: #fff;
    outline: none;
    height: 40px;
    width: 40px;
    font-size: 12px;
    margin: 0 15px 15px 0;
    cursor: pointer;
}
.sortStyle {
    position: absolute;
    right: 15px;
    top: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.sortStyle span {
    font-size: 12px;
    margin-right: 5px;
}
.productName {
    font-size: 12px;
    text-transform: capitalize;
}

ul {
    padding: 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start
}
.listwrap {
    width: 80%;
    padding-top: 15px;
    margin-bottom: 50px;
}
.sidebarWrapper h4 {
    margin-top: 0;
}
.content-wrapper {
    display: flex;
}
.clearBtn {
    display: block;
    margin-bottom: 15px;
    background: #fff;
    padding: 5px 10px;
    border: 1px solid #000;
    color: #000;
    border-radius: 5px;
    outline: none;
    cursor: pointer;
}
.sidebarWrapper {
    width: calc(20% - 40px);
    padding: 20px;
}
.productCount {
    font-size: 12px;
}
.selectboxStyle {
    height: 30px;
    width: 100px;
    border-radius: 0;
    outline: none;
    font-size: 12px;
    background: #fff;
}
.selectBoxWrapper {
    position: absolute;
    right: 40px;
    top: 50px;
    
}
.selectBoxWrapper span {
    font-size: 12px;
    margin-right: 5px;
}
.detailContent {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
}
.detailBox {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px 50px;
    background: #e2e2e2;
    text-transform: uppercase;
}
.detailBox p {
    font-size: 15px;
    font-weight: bold;
    border-bottom: 1px solid;
}
.backButton {
    font-size: 15px;
    position: absolute;
    z-index: 1;
    top: 50px;
    left: 15px;
}

